import { useState, useEffect } from "react";
import Sidebar from "./sidebar/Sidebar";
import ChatBot from "./chat/ChatBot";
import Login from "./auth/Login"; // Import the login component

export default function Home() {
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  useEffect(() => {
    // Check if token exists in localStorage
    const token = localStorage.getItem("token");
    setIsLoggedIn(!!token);
  }, []);

  const handleLogin = (token) => {
    localStorage.setItem("token", token);
    setIsLoggedIn(true);
  };

  const chats = [
    "ChatGPT UI Clone",
    "Optimizing AI Doctor Chatbot",
    "Build Chatbot with LLMs",
    "Deepseek-r1 Chatbot Setup",
    "Fix GPT-2 Chatbot Code",
    "AI Healthcare Solutions Ideas",
    "AI Healthcare Chatbot Design",
    "Find deepseek-r1 model",
    "DeepSeek R1 Query",
    "What is SNOMED CT",
    "Common health advice",
    "Vector Search in C++",
    "Hackathon Team Names",
  ];

  return (
    <>
      {!isLoggedIn ? (
        <Login onLogin={handleLogin} />
      ) : (
        <div className="grid grid-cols-[1fr_3fr] h-screen">
          <div className="bg-gray-900 text-white p-4">
            <Sidebar chats={chats} />
          </div>
          <div className="bg-gray-900 p-4">
            <ChatBot />
          </div>
        </div>
      )}
    </>
  );
}
